import React, { Component } from 'react';
import EventListener from 'react-event-listener';
import './Speaker.css';

const state_from_json = json => ({
  uid: json.uid,
  model_name: json.speaker_info.model_name,
  name: json.name,
  volume: json.volume,
  is_playing: json.is_playing
});

class Speaker extends Component {
  constructor(props) {
    super();
    this.state = {
      uid: props.uid || null,
      model_name: props.model_name || "??",
      name: props.name || "??",
      volume: props.volume || "??",
      is_playing: props.is_playing || {}
    };
    this.down = this.down.bind(this);
    this.up = this.up.bind(this);
    this.switchToTV = this.switchToTV.bind(this);
    this.update = this.update.bind(this);
    this.timeout = null;
  }

  postUpdate(update) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = {
      method: 'POST',
      headers: headers,
      body: JSON.stringify(update)
    };
    fetch('/api/v1/speakers/' + this.state.uid,
          options)
      .then(response => response.json())
      .then(json => this.setState(state_from_json(json)));
  }

  setVolume(newVolume) {
    this.postUpdate({ volume: newVolume });
  }

  down() {
    this.setVolume(this.state.volume - 1);
  }

  up() {
    this.setVolume(this.state.volume + 1);
  }

  switchToTV() {
    this.postUpdate({ is_playing: { tv: true }});
  }

  update() {
    if (this.props.match && this.state.uid) {
      fetch('/api/v1/speakers/' + this.state.uid)
        .then(response => response.json())
        .then(json => this.setState(state_from_json(json)));
      this.updateLater();
    }
  }

  updateLater() {
    if (this.timeout !== null)
      clearTimeout(this.timeout);
    this.timeout = setTimeout(this.update, 10000);
  }

  componentDidMount() {
    if (this.props.match) {
      const name_param = encodeURIComponent(this.props.match.params.speaker);
      fetch('/api/v1/speakers?name=' + name_param)
        .then(response => response.json())
        .then(json => {
          let best, best_ratio = -1;
          for (let speaker of json.speakers) {
            if (speaker.name_ratio > best_ratio) {
              best = speaker;
              best_ratio = speaker.name_ratio;
            }
          }
          this.setState(state_from_json(best));
          this.updateLater();
        });
    }
  }

  componentWillReceiveProps(newProps) {
    this.setState({
      volume: newProps.volume
    });
  }

  render() {
    if (!this.state.uid)
      return <div />;

    var switch_to_tv = null;
    if (this.state.model_name === "Sonos PLAYBASE") {
      if (!this.state.is_playing.tv) {
        switch_to_tv = (
          <button className="Speaker-switch_to_tv"
                  onClick={this.switchToTV}>
            Switch to TV
          </button>
        );
      }
    }

    return (
      <div className="Speaker"
           onClick={this.update}>
        <EventListener
          target="document"
          onFocus={this.update}
        />
        <div className="Speaker-info">
          <div className="Speaker-model_name">
            {this.state.model_name}
          </div>
          <div className="Speaker-name">
            {this.state.name}
          </div>
        </div>
        <div className="Speaker-volume">
          {this.state.volume}
        </div>
        {switch_to_tv}
        <button className="Speaker-volume_control Speaker-down"
                onClick={this.down}>-</button>
        <button className="Speaker-volume_control Speaker-up"
                onClick={this.up}>+</button>
      </div>
    );
  }
}

export default Speaker;



